html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header,
menu, nav, output, ruby, section, summary,
time, mark, audio, video, input{
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
}

body, html{
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif, 'Droid Sans Fallback';
  /* user-select: none ; // 禁用文本选中 */
  -webkit-tap-highlight-color: transparent;
  width: 100%;
  height: auto;
  margin: 0;
}

ul li{
  list-style-type: none;  
}/* 长文本隐藏 */

.text_hidden {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/*清除浮动*/

.clearfix:before {
  display: table;
  content: "";
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.clearfix:after {
  clear: both;
  display: table;
  content: "";
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/*1像素边框*/

.border_1px {
  position: relative;
}

.border_1px:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border: 1px solid rgba(51, 51, 255, 0.6);
  content: ' '
}

@media(-webkit-min-device-device-pixel-ratio: 1.5), (min-device-device-pixel-ratio: 1.5) {
  .border_1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
  }
}

@media(-webkit-min-device-device-pixel-ratio: 2), (min-device-device-pixel-ratio: 2) {
  .border_1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
  }
}